.single-page {
    // padding: 0 10px;
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: 30px auto;

    &__image {
        margin: 0 auto;
        width: 50%;
        &-img {
            width: 100%;
            height: auto;
            object-fit: cover;
            object-position: bottom; 
        }
    }

    &__card {
        display: flex;
        flex-direction: column;
        width: 100%;

        &-title {
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 30px;
        }

        &-description {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            font-size: 18px;
        }

        &-author {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            font-size: 20px;
        }
    }

    &__button {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

@media (max-width: 768px) {
    .single-page {
        &__image {
            width: 100%;
        }
    }
}