.form-page {
    width: 90%;
    margin: 0 auto;

    padding: 20px;

    form {
        label {
            // margin-top: 30px;
            input, textarea {
                margin-top: 10px;
                font-size:22px;
                display:block;
                width:100%;
                height:100%;
                padding:5px 10px;
                background:none;
                background-image:none;
                border:1px solid #a0b3b0;
                // color:#ffffff;
                border-radius:0;
                transition:border-color .25s ease, box-shadow .25s ease;
                
                &:focus {
                    outline:0;
                    border-color: #1ab188;
                }
            }
        }

        div {
            margin-top: 30px;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            
            input {
                padding:15px;
                background:rgba(#a0b3b0,.25);
                color:#a0b3b0;
                font-size:20px;
                float:left;
                width:50%;
                text-align:center;
                cursor:pointer;
                transition:.5s ease;
        
                &:hover {
                    background:darken(#1ab188,5%);
                    // color:#ffffff;
                }
            }
        }

    }
}